import React, { Component } from 'react';
import './App.css';
import NavHeader from './pages/Header';
import Home from './pages/Home';

//document.body.style.overflow="scroll";//显示页面水平和垂直滚动条 
// document.body.style.overflow="hidden";//隐藏页面水平和垂直滚动条  

class App extends Component {
  constructor(props) {
    super(props)
    window.theApp = this
    this.state = {
      windowHistory: [<Home />]
    }
    this.scores = []
    console.log("start app")
  }
  createScoreEntry(config) {
    let now = new Date()
    let entry = {config: config, date: now, score: {}}
    this.scores.push(entry)
  }
  saveScore(score) {
    let entry = this.scores[this.scores.length-1]
    entry.score = score
    console.log(this.scores)
  }
  getScores(qtype, type) {
    let list
    if (!qtype && !type) {
      list = this.scores
    }else{
      list = this.scores.filter((entry) => {
        return (!qtype || entry.config.qtype === qtype) && (!type || entry.config.type === type)
      })
    }
    return list
  }
  latestScore(qtype, type) {
    let list = this.getScores(qtype, type)
    if (list.length>0) {
      let score = list[list.length-1].score
      if (score.all>0) {
        let rate = Math.floor(100*(score.all - score.wrongs)/score.all)
        return `${score.all - score.wrongs}/${score.all} ${rate}分`
      }
    }
  }
  pushWindow(component) {
    let windowHistory = this.state.windowHistory
    windowHistory.push(component)
    this.setState({
      windowHistory: windowHistory
    })
  }
  popWindow() {
    let windowHistory = this.state.windowHistory
    if (windowHistory.length>1) {
      windowHistory.splice(windowHistory.length-1, 1)
      this.setState({
        windowHistory: windowHistory
      })
    }
  }
	render() {
		let defaultFontSize = window.device==="piano" ? 28 : 14
    if (window.device === "piano") {
      document.body.style.background = null
      document.body.style.color = 'white'
    }else{
      document.body.style.background = '#666'
      document.body.style.color = 'white'
    }
    let component = this.state.windowHistory[this.state.windowHistory.length-1]
    return (
			<div className="App" style={{fontSize: defaultFontSize}}>
				{window.device !== "piano" ? <NavHeader /> : null}
        {component}
			</div>
		);
	}
}

export default App;



